body {
    font-family: Monospace;
    background: rgb(206, 206, 206);
    color: #fff;
    margin: 0px;
  }
  h3 {
    font-weight: normal;
  }
  #info {
    color: #fff;
    position: absolute;
    top: 10px;
    width: 100%;
    text-align: center;
    z-index: 100;
    display: block;
  }
  #info a {
    color: #75ddc1;
    font-weight: bold;
  }